@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap');

body {
  padding:50px;
  background:url('../assets/images/bg.png') no-repeat center center;
  background-size:cover;
  font-family: 'DM Sans', sans-serif;
}


.box {
  position:relative;
  display:inline-block;
  background: #FFFFFF;  
  box-shadow: 0px 0px 16px rgba(58, 78, 88, 0.05);
  border-radius: 12px;
  padding:15px 30px;
  box-sizing:border-box;
  margin:25px 0px;
}

.box.full {
  width:100%;
}
.box.small {
  width:100%;
  max-width:375px;
  margin-left:auto;
  text-align:center;
}


.container {
  display:block;
  padding:15px 25px;
  max-width:1250px;
  margin:0px auto;
}

.meter {
  border-radius:10px;
  display:block;
  width:100%;
  height:23px;
  background:#f8f8f8;
  max-width:850px;
}

.meter > span {
  border-radius:10px;
  background: #0C3EE3;
  height:100%;
  display:block;
  width:auto;
}

.meter + div {
  width:225px;
  padding-left:10px;
}

.icon {
  max-width:50px;
  width:100%;
  padding:10px;
}

.flex {
  display:flex;
  align-items: center;
  flex-direction: row;

}

h3 {
  color:#222;
}

.tvl.auctions {
  height:auto ;
  min-height:486px;
}

.auctions p {
  padding:10px 0px;
  margin:0px;
}

.tvl h3 {
  padding-bottom:0px;
  margin-bottom:0px;
}
.network-header {
  background: #f8f8f8;
  padding: 0px 28px;
  margin: 0px -30px;
  line-height:0px;
}
.auctions .network {
  border-bottom:1px dashed #f2f2f2;
}
.auctions .network:last-of-type {
  border-bottom:0px;
}

.network-name {
  width:125px;
}

.network-name .icon {
  display:none;
} 

.network-cap, .network-auction , .network-total{
  width:90px;
  text-align:right;
}

.auctions {
  min-width:60%;
}

.selectors span {
  padding-right:35px;
  line-height:15px;
}

.selectors span + span {
  padding-right:0px;
}

.selectors span i {
  display:inline-block;
  width:16px;
  height:16px;
  background:#F0F3FB;
  border-radius:4px;
  vertical-align:middle;
  vertical-align:-2px;
  margin-right:5px;
}

.selectors .blue {  
  background:#0C3EE3;
}

.flex.small {
  min-width:150px;
  color:#5E6D7C;
  font-size:16px;
  padding:10px;
  box-sizing:border-box;
}

.icon img {
  width:100%;
  max-width:40px;
  margin:0px auto;

}

.tvl .recharts-responsive-container {
  margin-top:25px;
}

.justify {
  justify-content:space-between;
}

.score {
  max-width:250px;
}

.score-circle {
  stroke: #0C3EE3;
  transition: stroke-dashoffset 1s ease-out;
}

.js-circle {
  stroke-linecap: round;

}

h3 span {
  float:right;
  font-weight:normal;
  font-size:15px;
}

.score-empty {
  stroke: #f8f8f8;
}

.score-text {
  fill: #1e1935;
  font-size:60px;
  font-weight:bold;
}
.score-text.small {
  fill:#5E6D7C;
  font-size:25px;
}

.dominance canvas{
  margin:25px auto;
  max-width:250px;
  height:auto !important;
}

.variation {
  color:#1DA63B;
  font-size:75%;
  vertical-align:middle;
}

.giant {
  font-size: 80px;
  font-weight: 700;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  width:100%;
  padding-top: 25px;
  text-align:center;
  transform: translate(-50%,-50%);
}

p.big {
  font-size: 35px;
  font-weight: bold;
  margin:0px;
  vertical-align:middle;
}

.tvl {
  margin-right:50px;
  width:100%;
  max-width:825px;
  height:350px;
}

.dominance-market {
  text-align:left !important;
}

.dominance img {
  width:25px;
  left:0px;
  position:absolute;
}

.dominance > div {
  position:relative;
  padding-left:50px;
}

.crowdloantvl {
  margin:50px 0px;
}



.crowdloan-by{
  display:flex;
  align-items:center;
}


@media only screen and (max-width: 1200px)  {
  .globalTvl {
    margin:0px -25px;
  }
  body {
    padding:50px 25px;
  }
  p.big {
    //font-size:50px;
  }
  p.giant {
    font-size:50px;
  }
}

@media only screen and (max-width: 980px)  {
  .meter {
    max-width:100%;
  }
  .box {
    margin:15px 0px;
  }
  .flex {
    flex-wrap: wrap;
  }
  .tvl {
    height:auto;
  }
  p.big, p.giant {
    padding-top:0px;
    padding-bottom:10px;
    width:100%;
    text-align:center;
    position:relative;
    top:auto;
    left:auto;
    transform:none;
  }
  .flex > div {
    
      flex:1 0 100%;
      max-width:100% !important;
  }
}



@media only screen and (max-width: 765px)  { 
  body {
    padding:25px 15px;
  }
  .container {
    padding:0px;
  }
  p.big, p.giant {
    font-size:30px;
    text-align:left;
  }
  .meter {
    max-width:100%;
  }

  .full > div {
    text-align:center;
  }
  .full > div .flex {
    position:relative;
    margin-bottom:25px;
  }
  .icon {
    padding:0px;
    display:block;
    text-align:left;
  }
  .meter + div {
    position: absolute;
    right: 0px;
    top: 0px;
    padding-right:0px !important;
    text-align:right;
  }
  .network-name {
    width:50px;
    flex:auto !important;
  }
  .network-name .icon {
    display:block;
  } 
  .network-name span {
    display:none;
  }
  .network-header {
    flex-wrap: initial;
  }

  .network-header h4 {
    font-size:13px;
  }
  .auctions h3 span {
    display:block;
    width:100%;
    text-align:left;
    padding-bottom:25px;
  }
  .network-cap, .network-auction, .network-total {
    width:60px;
  }
  .network-auction {
    width:75px;
  }
  .selectors span {
    display:block;
    text-align:left;
    margin-bottom:15px;
  }
  .selectors span + span {
    margin-bottom:50px;
  }

  
}